/* тут все стили для боди  */
body {
  margin: 0;
  font-family: 'TT Lakes';
  background-color: #f8f8f8;
  font-style: normal;
  width: 100%; }

/* тут все логотипы  */
button {
  background-color: transparent;
  border: none;
  font-family: inherit; }

/* Убираем внутренние отступы */
ul {
  padding: 0; }

.page-header {
