  background-color: #ffffff;
  border-bottom: 2px solid #b8ffec;
  width: 100%; }
  .page-header__main-menu {
    display: flex;
    justify-content: space-between; }
    .page-header__main-menu .page-header__justify-left {
      display: flex;
      flex-direction: row;
      padding: 0; }
      .page-header__main-menu .page-header__justify-left .page-header__icon-menu {
        position: relative;
        margin-right: 16px;
        margin-left: 16px;
        padding: 0; }
      .page-header__main-menu .page-header__justify-left .page-header__icon-logo {
        position: relative;
        align-self: flex-start;
        margin-right: 16px;
        padding: 0; }
      .page-header__main-menu .page-header__justify-left .page-header__icon-divider {
        position: relative;
        margin-right: 16px;
        align-self: center;
        padding: 0; }
    .page-header__main-menu .page-header__justify-right {
      display: flex;
      padding: 0;
      justify-content: space-around; }
    .page-header__main-menu__icon-tools {
      position: relative;
      margin-right: 16px;
      order: 4;
      padding: 0; }
    .page-header__main-menu__icon-search {
      position: relative;
      margin-right: 16px;
      order: 4;
      padding: 0; }
    .page-header__main-menu__icon-call {
      position: relative;
      margin-right: 16px;
      order: -2;
      padding: 0; }
    .page-header__main-menu__icon-chat {
      position: relative;
      margin-right: 16px;
      order: 0; }
    .page-header__main-menu__icon-profile {
      position: relative;
      margin-right: 16px;
      order: 2; }
    .page-header__main-menu__icon-divider2 {
      position: relative;
      margin-right: 16px;
      order: 3; }

/* главный заголовок и его иконка */
.page-main {
  display: flex;
  flex-direction: column; }
  .page-main__highlight {
    display: flex;
    align-items: flex-end; }
  .page-main__title {
    margin-left: 12px;
    font-size: 28px;
    margin-bottom: 0;
    align-self: flex-end; }
  .page-main__title-icon {
    align-self: flex-end; }

/* кнопки */
.page-main__buttons {
  display: flex;
  list-style: none;
  margin-left: 0;
  padding-left: 0;
  overflow: scroll;
  margin-top: 24px;
  margin-left: 16px;
  margin-bottom: 0; }

.page-main .button-style {
  width: -moz-max-content;
  width: max-content;
  height: 40px;
  padding: 8px;
  border-radius: 5px;
  border: 2px transparent solid;
  background-color: #f8f8f8;
  color: #7e7e82;
  font-size: 0 16px;
  margin-right: 10px;
  font-family: 'TT Lakes';
  font-size: 16px;
  line-height: 24px; }

.page-main__buttons::-webkit-scrollbar {
  width: 0; }

.page-main .button-style:hover {
  border: 2px #b8ffec solid;
  background-color: #ffffff;
  color: #7e7e82; }

/* текст */
.main-information {
  display: flex;
  flex-direction: column; }

.page-main__text {
  margin: 0;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 0.20000000298023224px;
  text-align: left;
  padding: 0; }
  .page-main__text .paragraph320 {
    margin-bottom: 16px;
    margin-top: 16px;
    margin-left: 16px; }
  .page-main__text .img-styles1 {
    width: 90%; }

.second-title {
  display: flex;
  margin-top: 0;
  line-height: 24px;
  font-size: 16px;
  font-weight: 500;
  margin-left: 10px; }

.second-title-icon {
  display: inline;
  align-self: center; }

.swiper {
  min-width: 320px;
  height: auto; }

.swiper-slide {
  width: auto; }

.swiper-pagination {
  position: static; }

.swiper-pagination-bullet {
  background-color: #dddddd;
  width: 12px;
  height: 12px;
  margin-right: 12px; }

.swiper-pagination-bullet-active {
  background-color: #b5b6bc; }

.button-rotate {
  transform: rotate(180deg); }

.brands__list {
  margin-left: 32px;
  margin-right: 32px;
  padding: 0;
  row-gap: 16px;
  -moz-column-gap: 32px;
       column-gap: 32px; }

