  margin-left: 0;
  box-shadow: 10px 10px 30px 10px #0e185033;
  display: flex;
  flex-direction: column;
  /* шапка модалки */
  /* меню модалки */
  /* кнопки модалки */
  /* подвал модалки */ }
  .modal-style .link-style {
    color: inherit;
