@media (min-width: 767.95px) {
  .modal-container {
    display: none; } }

@media (min-width: 767px) and (max-width: 1439.95px) {
  .modal-menu {
    position: fixed;
    z-index: 999;
    top: 0;
    bottom: 0;
    right: 0;
    left: auto;
    width: 100%;
    height: 100%;
    background-color: rgba(255, 255, 255, 0.96); }
  .modal-container {
    display: block;
    position: sticky;
    top: 0;
    bottom: 0;
    height: 100%; }
  .modal-style {
    background-color: white;
    height: 100%; }
  .page-header {
    position: static;
    z-index: -1; }
  .modal {
    z-index: 9999; } }

@media (min-width: 320px) and (max-width: 767px) {
  .page-header {
    padding-right: 0;
    padding-left: 0; }
  .brands__list {
    min-width: 95%;
    margin-left: 16px;
    margin-right: 16px; } }
